import React, { useState } from 'react';
import { Form, Icon, Input, Button, Checkbox, message, Modal, Select } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import { injectIntl } from 'react-intl';
import { signin, setAuthToken, addBillingInfo } from './../../services';
import styled from 'styled-components';

import auth from './../../utils/auth';
import { config } from './../../config';
import './index.css';

const countries = require('./../../utils/countries').default;

const FormItem = styled(Form.Item)`
  &.ant-form-inline .ant-form-item {
    margin: 0 !important;
  }
`;

const Option = Select.Option;

const SignIn = props => {
  const {
    intl: { messages },
    signIn,
    history,
    form: { getFieldDecorator },
  } = props;
  const formValues = {
    name: '',
    address: '',
    country: 'ES',
    vattax: '',
  };
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);

  const handleReset = () => {
    props.form.resetFields();
  };

  const success = response => {
    const { accessToken } = response.data;

    message.success(messages.signin.success);
    auth.setToken(accessToken, true);
    signIn(response.data);
    setAuthToken(accessToken);
    handleReset();
    setLoading(false);

    if (response.data && response.data.needBillingInformation) {
      setVisible(true);
    } else {
      history.push('/dashboard');
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields(['email', 'password'], (err, values) => {
      if (!err) {
        const form = {
          email: values.email,
          password: values.password,
        };
        setLoading(true);

        signin(form)
          .then(response => {
            success(response);
          })
          .catch(error => {
            try {
              if (error.response.status === 404) {
                message.error(messages.signin.error.user);
              } else if (error.response.status === 401) {
                message.error(messages.signin.error.password);
              } else {
                message.error(messages.signin.error.all);
              }
            } catch (err) {
              message.error(err.message);
            }
            setLoading(false);
          });
      }
    });
  };

  const handleOk = e => {
    const { form, history } = props;
    try {
      form.validateFields(['name', 'address', 'country', 'vattax'], async (err, values) => {
        if (err) {
          return;
        }
        values.continent = countries[values.country].continent;
        await addBillingInfo(values);
        history.push('/dashboard');
        setVisible(false);
      });
    } catch (error) {
      // auth.clearAppStorage();
      history.push('/');
      setVisible(false);
    }
  };

  const handleCancel = e => {
    const { history } = props;
    auth.clearAppStorage();
    history.push('/');
    setVisible(false);
  };

  const options = Object.keys(countries).map(key => {
    return (
      <Option key={key} value={key}>
        {countries[key].name}
      </Option>
    );
  });

  return (
    <section className="form-card row no-gutters">
      <div
        className="form-card__img form-card__img--left col-lg-5"
        style={{
          backgroundImage: `url('${process.env.PUBLIC_URL}/images/bruno-martins-442125-unsplash.jpg')`,
        }}
      ></div>
      <div className="form-card__body col-lg-7 p-lg-6 p-4">
        <section className="form-v1-container">
          <h2>Login to Continue</h2>
          <p className="lead">
            Welcome back, sign in with your <strong>{config.pangeanic.title}</strong> account
          </p>
          <Form onSubmit={handleSubmit} className="form-v1">
            <FormItem>
              {getFieldDecorator('email', {
                rules: [
                  {
                    type: 'email',
                    message: messages.signin.rules.email.type,
                  },
                  {
                    required: true,
                    message: messages.signin.rules.email.required,
                  },
                ],
              })(
                <Input
                  size="large"
                  prefix={<Icon type="user" style={{ fontSize: 13 }} />}
                  placeholder="Email"
                />
              )}
            </FormItem>
            <FormItem>
              {getFieldDecorator('password', {
                rules: [
                  {
                    required: true,
                    message: messages.signin.rules.password.required,
                  },
                ],
              })(
                <Input
                  size="large"
                  prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
                  type="password"
                  placeholder={messages.signin.password}
                />
              )}
            </FormItem>
            <FormItem className="form-v1__remember">
              {getFieldDecorator('login1-remember', {
                valuePropName: 'checked',
                initialValue: true,
              })(<Checkbox>{messages.signin.rememberMe}</Checkbox>)}
            </FormItem>
            <FormItem>
              <Button
                loading={loading}
                type="primary"
                htmlType="submit"
                className="btn-cta btn-block"
              >
                {!loading ? messages.signin.btnSignIn : messages.signin.btnSignInLoad}
              </Button>
            </FormItem>
          </Form>
          <p className="additional-info">
            Don't have an account yet? <Link to="/main/signup">{messages.signin.register}</Link>
          </p>
          {/* <p className="additional-info">
            Forgot your username or password? <Link to="#">Reset password</Link>
          </p> */}
        </section>
      </div>
      <Modal title="Basic Modal" visible={visible} onOk={handleOk} onCancel={handleCancel}>
        <h3 style={{ textAlign: 'center' }}>{messages.profile.billinginformation}</h3>
        <Form style={{ margin: '0 auto' }} className="login-form">
          {/* nombre, direcci√≥n, pais, VAT/TAX number */}
          <Form.Item>
            {getFieldDecorator('name', {
              initialValue: formValues.name,
              rules: [
                {
                  required: true,
                  message: 'Please input your Name!',
                },
              ],
            })(<Input placeholder={messages.profile.name} />)}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('address', {
              initialValue: formValues.address,
              rules: [
                {
                  required: true,
                  message: 'Please input your Address!',
                },
              ],
            })(<Input placeholder={messages.profile.address} />)}
          </Form.Item>
          <Form.Item hasFeedback>
            {getFieldDecorator('country', {
              initialValue: formValues.country,
              rules: [
                {
                  required: true,
                  message: 'Please select your country!',
                },
              ],
            })(
              <Select
                showSearch
                optionFilterProp="children"
                filterOption={(input, option) =>
                  option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                placeholder={messages.profile.country}
              >
                {options}
              </Select>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('vattax', {
              initialValue: formValues.vattax,
              rules: [
                {
                  required: true,
                  message: 'Please input your VAT/TAX number!',
                },
              ],
            })(<Input placeholder={messages.profile.vattax} />)}
          </Form.Item>
        </Form>
      </Modal>
    </section>
  );
};

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default withRouter(WrappedSignIn);
